<template>
  <div class="container">
    <img class="banner" src="http://o80ronwlu.bkt.clouddn.com/banner.jpg" alt="banner">
    <el-input v-model="address" auto-complete="off" class="address-input" type="text" placeholder="请输入您的钱包地址"></el-input>
    <el-button type="primary" class="unlock-btn" @click.native="onConfirmClick">进入星云消息</el-button>
  </div>
</template>

<script>
export default {
  name: 'locked',
  data () {
    return {
      address: 'n1GHhGZ8PhZsYHfSSzbEdYHVNxE2esXBeLf'
    }
  },
  methods: {
    async onConfirmClick () {
      if (this.address) {
        store.address = this.address
        this.$router.push({name: 'list'})
      } else {
        this.$message('请输入地址')
      }
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  min-height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.banner {
  height: 300px;
  width: 300px;
  margin-top: 60px;
  border-radius: 10px;
}
.unlock-btn {
  width: 400px;
  margin-top: 25px;
}
.address-input {
  width: 500px;
  margin-top: 50px;
}
.dialog-wrapper {
  text-align: center;
}
</style>



