export async function getAccountInfo () {
  let accountState = await neb.api.getAccountState(store.address)
  accountState.balanceNas = Unit.fromBasic(accountState.balance, 'nas')
  return accountState
}

export function getGasPrice () {
  return neb.api.gasPrice()
}

export async function getDialyList () {
  let accountInfo = await getAccountInfo()
  return neb.api.call({
    from: store.address,
    to: store.contractAdddress,
    value: 0,
    nonce: accountInfo.nonce,
    gasPrice: store.gasPrice,
    gasLimit: '200000',
    contract: {
      function: 'getDiaryList',
      args: JSON.stringify([100, 0])
    }
  }).then(resp => JSON.parse(resp.result))
}

export async function getDialyDetail (title) {
  let accountInfo = await getAccountInfo()
  return neb.api.call({
    from: store.address,
    to: store.contractAdddress,
    value: 0,
    nonce: accountInfo.nonce,
    gasPrice: store.gasPrice,
    gasLimit: '200000',
    contract: {
      function: 'getDiary',
      args: JSON.stringify([title])
    }
  }).then(resp => JSON.parse(resp.result))
}

export async function setDiaryTest (title, content) {
  let accountInfo = await getAccountInfo()
  return neb.api.call({
    from: store.address,
    to: store.contractAdddress,
    value: 0,
    nonce: Number(accountInfo.nonce) + 1,
    gasPrice: store.gasPrice,
    gasLimit: '200000',
    contract: {
      function: 'setDiary',
      args: JSON.stringify([{
        title,
        content
      }])
    }
  })
}

